import React, {useCallback, useEffect, useRef} from 'react'
import {connect} from 'react-redux'

import TopPipeImage from '../images/pipe-top.svg'
import BottomPipeImage from '../images/pipe-bottom.svg'
import {useInView} from 'react-intersection-observer'
import Cloud from '../images/cloud/home/3.svg'
const Pipe = ({x, pipes, setScopeTime, scopeTime}) => {
  const windowSize = window.screen.height
  console.log(x)
  useEffect(()=>{
     console.log(pipes.length / 3)
    if (pipes.length > 2 && inView){

      setScopeTime(scopeTime + 1)
    }
  },[pipes.length])


  const ref = useRef() ;
  const {ref:inViewRef, inView } = useInView({threshold:0, triggerOnce: true})
  console.log(inView, 222222)
  // Используйте `useCallback`, чтобы не создавать заново функцию при каждом рендеринге
  const setRefs = useCallback (
    ( node )  =>  {
      // Ref из useRef должен иметь узел, назначенный `current`
      ref.current = node;
      inViewRef (node);
    },
    [inViewRef],
  );
    // useEffect(()=>{
    //   if (inView){
    //     setScopeTime(scopeTime + 1)
    //   }
    // },[inView])

  return (
    <div
      style={{
        position: 'relative',
      }}>

      {pipes.map(({topHeight, widthCloud, widthCloudTop}, i) => (

        <div
          key={`pipe-${i}`}
          style={{
            position: 'relative',
          }}>
          <div
            style={{
              backgroundSize:'cover',
              position: 'absolute',
              top: 0,
              left: x + i * 100,
              width: 107,
              height: topHeight,
              zIndex:200,
              background: `url(${TopPipeImage})`,
              backgroundPosition: 'bottom',
              transition: 'left 300ms',
            }}></div>
          <div  ref={setRefs} className={'listPipe'} style={{
            position:'absolute',
            top: topHeight,
            height:200,
            width: 107,
            left: x + i * 100,
          }} >
          </div>
          <img style={{
            position:'absolute',
            top: widthCloudTop,
            width: widthCloud,
            left: x + i * 100 + 120,
          }} src={Cloud} />


          <div
            style={{
              backgroundSize:'cover',
              position: 'absolute',
              top: topHeight + 200,
              left: x + i * 100,
              bottom: 0,
              width: 107,
              zIndex:200,
              height: `calc(${windowSize}px - ${topHeight}px - 300px)`,
              background: `url(${BottomPipeImage})`,
              transition: 'left 300ms',
            }}></div>
        </div>
      ))}
    </div>
  )
}

const mapStateToProps = ({pipe}) => ({x: pipe.x, pipes: pipe.pipes, widthCloud:pipe.widthCloud, widthCloudTop:pipe.widthCloudTop})
const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Pipe)
