import React, {useEffect, useRef, useState} from 'react'
import {connect} from 'react-redux'

import Bird from './Bird'
import Pipe from './Pipe'
import Foreground from './Foreground'

import BgImage from '../images/bg.png'
import Score from './Score'
import GameOver from './GameOver'

let gameLoop
let score = 0
// const [scores, setScores] = useState()
let pipeGenerator
const Game = ({status, start, fly}) => {
  const [statusGame, setStatusGame] = useState(false)
  const ref =useRef(null)
  const [scopeTime, setScopeTime] = useState(0)
  const pipes = useRef(null)
  if (status === 'game-over') {
    clearInterval(gameLoop)
    clearInterval(pipeGenerator)
  }



  useEffect(() => {
    if(ref && ref.current) {
      const handleKeyPress = () => {
        fly()
        if (status !== 'playing') {
          start()
        }
      }

      ref.current.addEventListener('click', handleKeyPress)
    }
  }, [statusGame])
  useEffect(()=>{
    if (statusGame === true) {
      setScopeTime(0)
      start()
    }
    if (status === 'game-over'){
      setStatusGame(false)
    }

  },[statusGame])

  return (
    <>
      {status === 'game-over' ?
        <GameOver scopeTime={scopeTime} setStatusGame={setStatusGame}/>
        :
        <div
          ref={ref}
          style={{
            position: 'relative',
            width: '100%',
            height: '100vh',
            background: `#45B3E9`,
            overflow: 'hidden',
          }}>
          <Score scopeTime={scopeTime} />
          <Bird />
          <Pipe scopeTime={scopeTime} setScopeTime={setScopeTime}/>
          <Foreground />
        </div>
      }
    </>
  )
}

const fly = () => {
  return (dispatch) => {
    dispatch({type: 'FLY'})
  }
}

const start = () => {
  return (dispatch, getState) => {
    const {status} = getState().game

    if (status !== 'playing') {
      gameLoop = setInterval(() => {
        dispatch({type: 'FALL'})
        dispatch({type: 'RUNNING'})

        check(dispatch, getState)
      }, 200)

      pipeGenerator = setInterval(() => {
        dispatch({type: 'GENERATE'})
      }, 4000)

      dispatch({type: 'START'})
    }
  }
}

const check = (dispatch, getState) => {
  const state = getState()

  const birdY = state.bird.y
  const pipes = state.pipe.pipes
  const x = state.pipe.x
  // console.log(x)
  const challenge = pipes
    .map(({topHeight}, i) => {
      return {
        x1: x + i * 100,
        y1: topHeight,
        x2: x + i * 100,
        y2: topHeight + 200,
      }
    })
    .filter(({x1,}) => {
      if (x1 > 0 && x1 < 288) {
        // if(y2 > birdY && y1 < birdY) {
        // }
        return true
      }

    })

  if (birdY > window.screen.height - 308) {
    dispatch({type: 'GAME_OVER'})
  }
  if (challenge.length) {
    const {x1, y1, x2, y2} = challenge[0]
    // console.log(x1, 66666)
    if (
      (x1 < 171 && 171 < x1 + 107 && birdY + 100 < y1) ||
      (x2 < 171 && 171 < x2 + 107 && birdY + 100 > y2)
    ) {
      dispatch({type: 'GAME_OVER'})
      // console.log(111111)
    }
  }
}

const mapStateToProps = ({game}) => ({status: game.status})
const mapDispatchToProps = {start, fly}

export default connect(mapStateToProps, mapDispatchToProps)(Game)
